import {Injectable} from "@angular/core";
import {Observable} from "rxjs";
import {File} from "@ionic-native/file";
import {FileTransfer, FileTransferObject} from "@ionic-native/file-transfer";
import {AlertController} from "ionic-angular";
import {Logger} from "./Logger";
import {APP_VERSION_SERVE_URL, FILE_SERVE_URL} from "./Constants";
import {Utils} from "./Utils";
import {HttpService} from "./HttpService";
import {NativeService} from "./NativeService";
import {FileService} from "./FileService";

@Injectable()
export class VersionService {
  appName;//app idcom.kit.ionic2tabs,appNameionic2tabs
  appType;//android  ios
  currentVersionNo;//
  latestVersionNo;//
  lastVersionInfo;//app
  versions;//app

  isMobile = false;//

  appDownloadPageUrl;//
  apkUrl;//android apk

  //app.0,app
  updateProgress: number = -1;

  isInit: boolean = true; //

  constructor(public nativeService: NativeService,
              public transfer: FileTransfer,
              public file: File,
              public httpService: HttpService,
              public fileService: FileService,
              public alertCtrl: AlertController,
              public logger: Logger) {

  }

  init() {
    this.isMobile = this.nativeService.isMobile();
    if (this.isMobile) {

      this.nativeService.getVersionNumber().subscribe(currentVersionNo => {
        this.currentVersionNo = currentVersionNo;
      });

      this.nativeService.getPackageName().subscribe(packageName => {
        this.appName = packageName.substring(packageName.lastIndexOf('.') + 1);
        this.appType = this.nativeService.isAndroid() ? 'android' : 'ios';

        this.appDownloadPageUrl = FILE_SERVE_URL + '/static/download.html?name=' + this.appName;

        //app
        let url = Utils.formatUrl(`${APP_VERSION_SERVE_URL}/v1/apply/getDownloadPageByEName/${this.appName}/${this.appType}`);
        this.httpService.get(url).subscribe(res => {
          this.isInit = false;//
          if (res && res.code == 1 && res.data && res.data.lastVersion) {
            let data = res.data;
            this.lastVersionInfo = data.lastVersion;
            this.latestVersionNo = data.lastVersion.version;
            for (let fileRelation of data.fileRelationList) {
              if (fileRelation.type == 'apk') {
                this.fileService.getFileInfoById(fileRelation.fileId).subscribe(res => {
                  this.apkUrl = res.origPath;
                })
              }
            }
          }
        }, err => {
          this.logger.log(err, '', {url: url});
          this.isInit = false;//
        })
      });
    }
  }

  getCurrentVersionNo() {
    return this.currentVersionNo;
  }

  getLatestVersionNo() {
    return this.latestVersionNo;
  }

  getLastVersionInfo() {
    return this.lastVersionInfo;
  }

  /**
   * 
   */
  assertUpgrade() {
    if (this.isMobile) {
      if (this.isInit) {
        setTimeout(() => {
          this.assertUpgrade();
        }, 5000);
      } else {
        if (this.latestVersionNo && (this.currentVersionNo != this.latestVersionNo)) {
          let that = this;
          if (this.lastVersionInfo.isForcedUpdate == 1) {//
            this.alertCtrl.create({
              title: '',
              subTitle: '',
              enableBackdropDismiss: false,
              buttons: [{
                text: '', handler: () => {
                  that.downloadApp();
                }
              }
              ]
            }).present();
          } else {
            this.alertCtrl.create({
              title: '',
              subTitle: ',',
              enableBackdropDismiss: false,
              buttons: [{text: ''}, {
                text: '', handler: () => {
                  that.downloadApp();
                }
              }]
            }).present();
          }
        }
      }
    }
  }

  /**
   * app
   */
  downloadApp() {
    if (this.nativeService.isIos()) {//ios
      this.nativeService.openUrlByBrowser(this.appDownloadPageUrl);
    }
    if (this.nativeService.isAndroid()) {//android
      if (!this.apkUrl) {
        this.nativeService.alert('android apk');
        return;
      }
      this.nativeService.externalStoragePermissionsAuthorization().subscribe(() => {
        let backgroundProcess = false;//
        let alert = this.alertCtrl.create({//
          title: '0%',
          enableBackdropDismiss: false,
          buttons: [{
            text: '', handler: () => {
              backgroundProcess = true;
            }
          }
          ]
        });
        alert.present();
        const fileTransfer: FileTransferObject = this.transfer.create();
        const apk = this.file.externalRootDirectory + 'download/' + `android_${Utils.getSequence()}.apk`; //apk
        //apk
        fileTransfer.download(this.apkUrl, apk).then(() => {
          window['install'].install(apk.replace('file://', ''));
        }, err => {
          this.updateProgress = -1;
          alert && alert.dismiss();
          this.logger.log(err, 'android app ');
          this.alertCtrl.create({
            title: '',
            subTitle: '',
            buttons: [{
              text: '', handler: () => {
                this.nativeService.openUrlByBrowser(this.appDownloadPageUrl);//
              }
            }
            ]
          }).present();
        });

        let timer = null;//onProgress,setTimeout
        fileTransfer.onProgress((event: ProgressEvent) => {
          let progress = Math.floor(event.loaded / event.total * 100);//
          this.updateProgress = progress;
          if (!backgroundProcess) {
            if (progress === 100) {
              alert && alert.dismiss();
            } else {
              if (!timer) {
                timer = setTimeout(() => {
                  clearTimeout(timer);
                  timer = null;
                  let title = document.getElementsByClassName('alert-title')[0];
                  title && (title.innerHTML = `${progress}%`);
                }, 1000);
              }
            }
          }
        });
      })
    }
  }

  /**
   * 
   */
  checkNewVersion() {
    if (this.updateProgress == -1 || this.updateProgress == 100) {
      if (this.currentVersionNo != this.latestVersionNo) {
        this.assertUpgrade();
      } else {
        this.nativeService.alert('');
      }
    } else {//
      let alert = this.alertCtrl.create({
        title: `${this.updateProgress}%`,
        buttons: [{text: ''}]
      });
      alert.present();
      let interval = setInterval(() => {
        alert.setTitle(`${this.updateProgress}%`);
        if (this.updateProgress == 100) {
          clearInterval(interval);
          alert && alert.dismiss();
        }
      }, 1000);
    }
  }

  /**
   * app
   */
  getVersionList() {
    if (this.isMobile) {
      let url = Utils.formatUrl(`${APP_VERSION_SERVE_URL}/v1/apply/findVersionList/${this.appName}/${this.appType}`);
      return this.httpService.get(url).map(res => {
        if (res && res.code == 1) {
          return res.data.versions || [];
        }
      })
    } else {
      return Observable.of([]);
    }
  }

}



