import {Injectable} from '@angular/core';

@Injectable()
export class GlobalData {

  private _userId: string;//id
  private _phone: string;//
  private _user;//

  private _token: string;//token

  //httploading,:true,loading,false
  private _showLoading: boolean = true;

  //
  private _enabledFileCache: boolean = true;

  get userId(): string {
    return this._userId;
  }

  set userId(value: string) {
    this._userId = value;
  }

  get phone(): string {
    return this._phone;
  }

  set phone(value: string) {
    this._phone = value;
  }

  get user() {
    return this._user;
  }

  set user(value) {
    this._user = value;
  }

  get token(): string {
    return this._token;
  }

  set token(value: string) {
    this._token = value;
  }

  get showLoading(): boolean {
    return this._showLoading;
  }

  set showLoading(value: boolean) {
    this._showLoading = value;
  }
  get enabledFileCache(): boolean {
    return this._enabledFileCache;
  }

  set enabledFileCache(value: boolean) {
    this._enabledFileCache = value;
  }

}



